<template>
    <div id="orders">
        <div class="orders-container-fluid container-fluid">
            <div class="orders-container container">
                <div class="track-by-phone-container">
                    <div class="track-by-phone">
                        <div class="title col-6">
                            <h5>track your orders</h5>
                        </div>
                        <div class="search-order col-6">
                            <input
                                type="text"
                                placeholder="track your order by phone number"
                                v-model="phone"
                            />
                            <button @click="getOrdersByPhone()">Check</button>
                        </div>
                    </div>
                </div>
                <div
                    class="orders-details-container"
                    v-for="(order, index) in user_orders"
                    :key="order.id"
                >
                    <div class="order-info-container">
                        <div class="info col-8">
                            <h5>order info</h5>
                            <ul>
                                <li>order : {{ order.id }}</li>
                                <li>
                                    total :
                                    {{ order.total_price_after_discount }} LE
                                </li>
                                <li>items : {{ order.order_items.length }}</li>
                            </ul>
                        </div>
                        <div class="order-statu col-4">
                            <div class="statu-head">
                                <h5>status</h5>
                            </div>
                            <div class="statue">
                                <span>{{ order.state }}</span>
                            </div>
                        </div>
                    </div>
                    <div class="order-tracking-container">
                        <div class="tracking-info">
                            <h1>order status : {{ order.state }}</h1>
                            <h5>
                                expected delivary :
                                <span>{{ order.receiving_date }}</span>
                            </h5>
                        </div>
                        <div class="order-tracking">
                            <div class="status-container">
                                <div
                                    class="statu"
                                    :class="{ done: order.state != 'restored' }"
                                >
                                    <span></span>
                                    <h5>waitting</h5>
                                </div>
                                <div
                                    class="statu"
                                    :class="
                                        order.state == 'confirmed'
                                            ? 'done'
                                            : order.state == 'dispatched'
                                            ? 'done'
                                            : order.state == 'delivered'
                                            ? 'done'
                                            : ''
                                    "
                                >
                                    <span></span>
                                    <h5>confirmed</h5>
                                </div>
                                <div
                                    class="statu"
                                    :class="
                                        order.state == 'dispatched'
                                            ? 'done'
                                            : order.state == 'delivered'
                                            ? 'done'
                                            : ''
                                    "
                                >
                                    <span></span>
                                    <h5>dispatched</h5>
                                </div>
                                <div
                                    class="statu"
                                    :class="
                                        order.state == 'delivered' ? 'done' : ''
                                    "
                                >
                                    <span></span>
                                    <h5>delivered</h5>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="order-items-container">
                        <div class="item-container">
                            <div
                                class="order-item"
                                v-for="item in order.order_items"
                                :key="item.id"
                            >
                                <div class="item-img">
                                    <img
                                        :src="
                                            'https://queen-store.net/azshop/public_html/board/public_html/items-photos/' +
                                                item.photo_link
                                        "
                                        alt=""
                                    />
                                </div>
                                <div class="item-info">
                                    <ul>
                                        <li>{{ item.name }}</li>
                                        <li>quantity : {{ item.quantity }}</li>
                                        <li>
                                            price : {{ item.selling_price }} LE
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="order-shipping-details-container">
                        <div class="order-shipping-details">
                            <div class="shipping-header">
                                <h5>shipping info</h5>
                            </div>
                            <div class="order-shipping-info">
                                <h5>shipping details</h5>
                                <ul>
                                    <li>
                                        governorate :
                                        {{ orders_address[index].governorate }}
                                    </li>
                                    <li>
                                        district :
                                        {{ orders_address[index].district }}
                                    </li>
                                    <li>
                                        street :
                                        {{ orders_address[index].street }}
                                    </li>
                                    <li>
                                        floor :
                                        {{ orders_address[index].floor }}
                                    </li>
                                    <li>
                                        apartment :
                                        {{ orders_address[index].apartment }}
                                    </li>
                                    <li>
                                        phone number : +2{{
                                            orders_address[index].phone
                                        }}
                                    </li>
                                    <li>
                                        total price :
                                        {{ order.total_price_after_discount }} L.E
                                    </li>
                                    <li>discount : {{ order.discount }} %</li>
                                    <li>delievary : {{ order.delivery }}</li>
                                    <li>status : {{ order.state }}</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div
                        class="action-container"
                        v-if="order.state != 'restored'"
                    >
                        <div class="action">
                            <button @click="confirm_action = true">
                                Cancel Order {{order.id}}
                            </button>
                        </div>
                        <div class="action-confirm" v-if="confirm_action">
                            <div class="message-box">
                                <div class="confirm-massage">
                                    <h5>are you sure to delete this order</h5>
                                </div>
                                <div class="confirm-action">
                                    <button @click="confirm_action = false">
                                        cancel
                                    </button>
                                    <button @click="deleteOrder(order.id)">
                                        delete
                                        {{order.id}}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="error-message" v-if="orders == false">
                    <h1>
                        <font-awesome-icon :icon="['fas' , 'exclamation-triangle']" />
                        there are no orders with this phone number</h1>
                </div>
            </div>
        </div>
    </div>
</template>
<style lang="scss">
@import "../style/css/scss/views/_orders.scss";
</style>
<script>
import axios from "axios";
export default {
    data() {
        return {
            user_token: "",
            user_orders: [],
            confirm_action: false,
            orders_address: [],
            phone: "",
            orders: true
        };
    },
    methods: {
        deleteOrder(id) {
            axios
                .delete(
                    "https://queen-store.net/azshop/public_html/board/public_html/api/orders/" +
                        id,
                    { headers: { Authorization: "Bearer " + this.user_token } }
                )
                .then(() => {
                    this.confirm_action = false;
                    axios
                        .get(
                            "https://queen-store.net/azshop/public_html/board/public_html/api/orders",
                            {
                                headers: {
                                    Authorization: "Bearer " + this.user_token
                                }
                            }
                        )
                        .then(res => {
                            this.user_orders = res.data.orders;
                            axios
                                .get(
                                    "https://queen-store.net/azshop/public_html/board/public_html/api/addresses",
                                    {
                                        headers: {
                                            Authorization:
                                                "Bearer " + this.user_token
                                        }
                                    }
                                )
                                .then(res => {
                                    this.orders_address = [];
                                    for (
                                        var i = 0;
                                        i < this.user_orders.length;
                                        i++
                                    ) {
                                        for (
                                            var x = 0;
                                            x < res.data.addresses.length;
                                            x++
                                        ) {
                                            if (
                                                this.user_orders[i]
                                                    .address_id ==
                                                res.data.addresses[x].id
                                            ) {
                                                this.orders_address.push(
                                                    res.data.addresses[x]
                                                );
                                            }
                                        }
                                    }
                                });
                        });
                });
        },
        getOrdersByPhone() {
            if (this.phone != "") {
                axios
                    .get(
                        "https://queen-store.net/azshop/public_html/board/public_html/api/phone_orders/" +
                            this.phone
                    )
                    .then((res) => {
                        this.user_orders = res.data
                        if(res.data.length == 0){
                            this.orders = false
                        }
                    });
            }
        }
    },
    mounted() {
        this.user_token = localStorage.getItem("user_token");
        if (this.user_token != "null") {
            axios
                .get(
                    "https://queen-store.net/azshop/public_html/board/public_html/api/orders",
                    { headers: { Authorization: "Bearer " + this.user_token } }
                )
                .then(res => {
                    this.user_orders = res.data.orders;
                    axios
                        .get(
                            "https://queen-store.net/azshop/public_html/board/public_html/api/addresses",
                            {
                                headers: {
                                    Authorization: "Bearer " + this.user_token
                                }
                            }
                        )
                        .then(res => {
                            for (var i = 0; i < this.user_orders.length; i++) {
                                for (
                                    var x = 0;
                                    x < res.data.addresses.length;
                                    x++
                                ) {
                                    if (
                                        this.user_orders[i].address_id ==
                                        res.data.addresses[x].id
                                    ) {
                                        this.orders_address.push(
                                            res.data.addresses[x]
                                        );
                                    }
                                }
                            }
                        });
                });
        }
    }
};
</script>
