import axios from "axios";
export const getCategories = ({ commit }) => {
    axios.get('https://queen-store.net/azshop/public_html/board/public_html/api/categories')
    .then(res => {
        commit('GET_CATEGORIS', res.data)
    });
};
export const getRecommandedItems = ({ commit }) => {
    axios.get('https://queen-store.net/azshop/public_html/board/public_html/api/items')
    .then(res => {
        commit('GET_RECOMMANDEDPRODUCTS', res.data)
    });
}
export const getWishlistNO = ({commit}) => {
    axios.get(('https://queen-store.net/azshop/public_html/board/public_html/api/carts') , {headers: {'Authorization': `Bearer ` + localStorage.getItem('user_token')}})
    .then((res) => {
        commit('GET_WISHLISTITEMSNO' , res.data.items)
    });
}
