<template>
    <div id="contact" class="contact-us-container container-fluid" :class="{ 'contact-fix' : scrollPosition > 0 }">
        <div class="contact-us container">
            <div class="logo left col-md-6 d-md-inline-block d-none">
                <img src="../assets/logo.svg" alt="" srcset="">
            </div>
            <div class="contact-form right col-md-6 col-12">
                <form>
                    <span>contact us</span><br>
                    <input type="text" name="" id="" placeholder="your name" class="input-name" v-model="name">
                    <input type="text" name="" id="" placeholder="mail address" v-model="email">
                    <input type="number" name="" id="" placeholder="phone number" v-model="phone">
                    <textarea name="" id="" cols="30" rows="10" placeholder="Text massage" v-model="description"></textarea>
                    <button @click="submitForm">send massege</button>
                </form>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
    @import "../style/css/scss/views/_contact-us.scss";
</style>

<script>
import axios from 'axios';
export default {
        el: "#contact",
    data (){
        return {
            scrollPosition: null,
            name:"",
            phone:"",
            email:"",
            description:""
        }
        
    },
    methods: {
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
        submitForm(e){
            const dtta = {
                name:this.name,
                email:this.email,
                phone:this.phone,
                description:this.description
            }
            axios
            .post(
                'https://queen-store.net/azshop/public_html/board/public_html/api/contactus',
                dtta,
            ).then((res) => {
                console.log(res)
            })
            e.preventDefault();
        }
    },
    mounted() {
        window.addEventListener("scroll", this.updateScroll);
    }
}
</script>