import Vue from "vue";
import VueRouter from "vue-router";
import Contact from "../views/contact.vue";
import OrderForm from "../views/orderForm.vue";
import MyAccount from "../views/myaccount.vue";
import Order from "../views/orders.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Home",
        component: () => import(/*webpackChunckName: "Home"*/"../views/Home.vue")
    },
    {
        path: "/products",
        name: "products",
        component: () => import(/*webpackChunckName: "Products"*/"../views/Products.vue")
    },
    {
        path: "/product/:itemId",
        name: "product",
        component: () => import(/*webpackChunckName: "Product"*/"../views/Product.vue")
    },
    {
        path: "/wishlist",
        name: "wishlist",
        component: () => import(/*webpackChunckName: "wishlist"*/"../views/wish.vue")
    },
    {
        path:"/contact-us",
        name:"contact",
        component : Contact
    },
    {
        path:"/order-form",
        name: "orderForm",
        component: OrderForm
    },
    {
        path:"/account",
        name: "myaccount",
        component: MyAccount
    },
    {
        path:"/orders",
        name:"orders",
        component:Order
    }
];

const router = new VueRouter({
    mode: "hash",
    base: process.env.BASE_URL,
    routes
});

export default router;
