<template>
    <div id="user-page">
        <div class="user-account-container container-fluid" v-bind:class="{ 'fix-account': scrollPosition > 0 }">
            <div class="user-account container">
                <div class="user-account-header">
                </div>
                <div class="user-details-nav">
                    <div class="user-img col-md-6 col-12">
                        <div class="img">
                            <img src="../assets/1.jpg" alt="">
                        </div>
                        <div class="user-name">
                            <div class="inf">
                            <span>{{user_name}}</span>
                            <span>{{email}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="user-cart col-6 d-md-block d-none">
                        <ul>
                            <li><font-awesome-icon :icon="['fas' , 'heart']" /></li>
                            <li><font-awesome-icon :icon="['fas' , 'shopping-basket']" /></li>
                        </ul>
                        <span>balance:  00.0</span>
                    </div>
                </div>
                <div class="user-details">
                    <ul class="details-container">
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">username</li>
                                <li class="col-4 inf">
                                    {{user_name}}
                                    <input type="text" v-model="user_name" class="edit">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setName($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">email</li>
                                <li class="col-4 inf">
                                    {{email}}
                                    <input type="text" v-model="email">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setMail($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">phone</li>
                                <li class="col-4 inf">
                                    {{phone}}
                                    <input type="text" v-model="phone">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setPhone($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">country</li>
                                <li class="col-4 inf">
                                    {{country}}
                                    <input type="text" v-model="country">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setAddress($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">governorate</li>
                                <li class="col-4 inf">
                                    {{governorate}}
                                    <input type="text" v-model="governorate">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setAddress($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">street</li>
                                <li class="col-4 inf">
                                    {{street}}
                                    <input type="text" v-model="street">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setAddress($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">floor</li>
                                <li class="col-4 inf">
                                    {{floor}}
                                    <input type="text" v-model="floor">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setAddress($event)"/>
                                </li>
                            </ul>
                        </li>
                        <li class="detail">
                            <ul class="details">
                                <li class="col-4">apartment</li>
                                <li class="col-4 inf">
                                    {{apartment}}
                                    <input type="text" v-model="apartment">
                                </li>
                                <li class="col-4 icon">
                                    <font-awesome-icon :icon="['fas' , 'edit']" class="show" @click="editData($event)"/>
                                    <font-awesome-icon :icon="['fas' , 'check']" @click="setAddress($event)"/>
                                </li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
    @import "../style/css/scss/views/account.scss";
</style>

<script>
import axios from 'axios';
export default {
    data(){
        return{
            scrollPosition: null,
            user_token:"",
            user_name:"",
            email:"",
            phone:"",
            country:"",
            apartment:"",
            floor:"",
            governorate:"",
            street:"",
        }
    },
    methods:{
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
        editData(event){
            var inp = event.target.parentNode.parentNode.parentNode.children[1].children[0];
            var ic_1 = event.target.parentNode.parentNode.parentNode.children[2].children[0];
            var ic_2 = event.target.parentNode.parentNode.parentNode.children[2].children[1];
            ic_1.classList.toggle('show');
            ic_2.classList.toggle('show');
            inp.classList.toggle('show');
        },
        setName(event){
            const data = {
                        full_name:this.user_name
                    } 
            axios.post(
                ("https://queen-store.net/azshop/public_html/board/public_html/api/users"),
                data,
                {headers: {'Authorization': 'Bearer ' + this.user_token }}
            ).then(()=>{
                this.editData(event);
            })
        },
        setMail(event){
            const data = {
                        email:this.email
                    } 
            axios.post(
                ("https://queen-store.net/azshop/public_html/board/public_html/api/users"),
                data,
                {headers: {'Authorization': 'Bearer ' + this.user_token }}
            ).then(()=>{
                this.editData(event);
            })
        },
        setPhone(event){
            const data = {
                        phone:this.phone
                    } 
            axios.post(
                ("https://queen-store.net/azshop/public_html/board/public_html/api/users"),
                data,
                {headers: {'Authorization': 'Bearer ' + this.user_token }}
            ).then(()=>{
                this.editData(event);
            })
        },
        setAddress(event){
            const data = {
                        apartment:this.apartment,
                        country:this.country,
                        floor:this.floor,
                        street:this.street,
                        governorate:this.governorate,
                        default:1
                    } 
            axios.put(
                ("https://queen-store.net/azshop/public_html/board/public_html/api/addresses/0"),
                data,
                {headers: {'Authorization': 'Bearer ' + this.user_token }}
            ).then(()=>{
                this.editData(event);
            })
        }

    },
    mounted() {
        window.addEventListener("scroll", this.updateScroll);
        this.user_token = localStorage.getItem('user_token');
        axios.get(
            ('https://queen-store.net/azshop/public_html/board/public_html/api/user'),
            {headers: {'Authorization': 'Bearer ' + this.user_token }}
        ).then((res)=>{
            this.user_name = res.data.user.full_name;
            this.email = res.data.user.email;
            this.phone = res.data.user.phone;
        });

        axios.get(
            ('https://queen-store.net/azshop/public_html/board/public_html/api/addresses'),
            {headers: {'Authorization': 'Bearer ' + this.user_token }}
        ).then((res)=>{
            this.apartment = res.data.addresses[0].apartment;
            this.country = res.data.addresses[0].country;
            this.floor = res.data.addresses[0].floor;
            this.street = res.data.addresses[0].street;
            this.governorate = res.data.addresses[0].governorate;
        })

    }
}
</script>