<template>
    <div id="order-form" :class="{ 'order-form-fix': scrollPosition > 0 }">
        <div class="order-form-container container-fluid">
            <div class="order-form container">
                <div class="order-form-header">
                    <div class="header">
                        <span>continue order</span>
                    </div>
                    <div class="order-form-sub-header d-none">
                        <div>
                            <span>do you have vauchor copon ?</span>
                        </div>
                        <div>
                            <input
                                type="text"
                                name=""
                                id=""
                                placeholder="Code Here .."
                            />
                            <button>send</button>
                        </div>
                    </div>
					     <div class="order-form-sub-header">
                        <div>
                            <span>Check Your Region Delivery Fees ?</span>
                        </div>
                        <div>
                            <select id="CostList" @change="UpdateCost" >
                                <option value="0" selected disabled
                                    >Select Region</option
                                >
                                <option 
                                    v-for="district in districts"
                                    :key="district.id"
									:data-cost="district.cost"
                                    >
									{{
                                
                                            district.name
                                    }}
									
									</option
                                > </select
                            >
							
							<input
                                type="text"
                                name=""
                                id="CostInput"
                                placeholder="Cost Here" style="margin-left:10px; width:150px;"
                            />
                        </div>
                    </div>
                </div>
                <div class="order-form-info-sec col-md-6 col-12">
                    <div class="order-info-header">
                        <span>order details</span>
                    </div>
                    <div class="form-info">
                        <form action="">
                            <label for="">old address</label><br />
                            <select v-model="selectedAddressValue">
                                <option value="0" selected disabled
                                    >select address</option
                                >
                                <option
                                    :value="address.id"
                                    v-for="address in userAddress"
                                    :key="address.id"
                                    >{{
                                        address.district +
                                            " - " +
                                            address.street +
                                            address.floor +
                                            " - " +
                                            address.governorate +
                                            " - " +
                                            address.country
                                    }}</option
                                > </select
                            ><br />
                            <label for="">full name</label><br />
                            <input
                                type="text"
                                name=""
                                id=""
                                v-model="name"
                            /><br />
                            <label for="">phone number</label><br />
                            <input
                                type="text"
                                name=""
                                id=""
                                v-model="phone_number"
                                placeholder="+201*********"
                            /><br />
                            <label for="">country</label><br />
                            <input type="text" v-model="country" /><br />
                            <label for="">governorate</label><br />
                            <select name="" id="" v-model="governorate">
                                <option value="cairo" selected>cairo</option>
                                <option value="alex">alex</option> </select
                            ><br />
                            <label for="">district</label><br />
                            <input type="text" v-model="district" /><br />
                            <label for="">street</label><br />
                            <input type="text" v-model="street" /><br />
                            <label for="">floor</label><br />
                            <input type="text" v-model="floor" /><br />
                            <label for="">appartments</label><br />
                            <input type="text" v-model="appartment" /><br />
                            <label for="">order notes</label><br />
                            <textarea
                                name=""
                                id=""
                                cols="30"
                                rows="10"
                                v-model="order_notes"
                                placeholder="Order notes .."
                            ></textarea>
                        </form>
                    </div>
                </div>
                <div class="order-form-details-sec col-md-6 col-12">
                    <div class="details-view">
                        <div class="order-form-details-header">
                            <span>order details</span>
                        </div>
                        <div class="order-details">
                            <ul>
                                <li>product details</li>
                                <li>
                                    <div
                                        class="details"
                                        v-for="item in cartItems"
                                        :key="item.id"
                                    >
                                        <ul class="col-9">
                                            <li>{{ item.name }}</li>
                                            <li>
                                                quantity : {{ item.quantity }}
                                            </li>
                                            <li>size : {{ item.size }}</li>
                                            <li>
                                                price :
                                                {{ item.selling_price }} LE
                                            </li>
                                        </ul>
                                        <div class="photo col-3">
                                            <img
                                                :src="
                                                    'https://queen-store.net/azshop/public_html/board/public_html/items-photos/' +
                                                        item.photo_link
                                                "
                                                alt="product"
                                            />
                                        </div>
                                    </div>
                                </li>
                                <li>
                                    <ul>
                                        <li>items price</li>
                                        <li>{{ total_before_shipping }} LE</li>
                                    </ul>
                                </li>
                                <li>
                                    <ul>
                                        <li>delivary cost</li>
                                        <li>{{ delivery }} LE</li>
                                    </ul>
                                </li>
                                <li>
                                    <ul>
                                        <li>total</li>
                                        <li>
                                            {{
                                                total_before_shipping +
                                                    delivery
                                            }}
                                            LE
                                        </li>
                                    </ul>
                                </li>
                            </ul>
                            <div class="order-notes">
                                <span>notes:</span>
                                <h5>cash on delivery</h5>
                                <p>
                                </p>
                                <button @click="order">check out</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
@import "../style/css/scss/views/_orderForm.scss";
</style>
<script>
import axios from "axios";
export default {
    el: "#order-form",
    data() {
        return {
            scrollPosition: null,
            name: "",
            phone_number: "",
            governorate: "",
            full_address: "",
            discount: 0,
            country: "",
            district: "",
            street: "",
            floor: "",
            appartment: "",
            total: 100,
            order_notes: "",
            user_token: "",
            user_id: "",
            address_id: "",
            delivery: 0,
            total_before_shipping: 0,
            selected_address: "",
            cartItems: [],
            userAddress: [],
            selectedAddressValue: "0",
            districts:[]
        };
    },
    methods: {
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
		UpdateCost(){
		var e = document.getElementById("CostList");
        var value = e.options[e.selectedIndex].getAttribute("data-cost");
		document.getElementById("CostInput").value=value + " " +" L.E ";
		this.delivery=parseInt(value);
        //this.selectedDistrictCost=parseInt(value);

		
		},
        order() {
            if (this.selectedAddressValue == 0) {
                const dd = {
                    country: this.country,
                    governorate: this.governorate,
                    district: this.district,
                    street: this.street,
                    floor: this.floor,
                    apartment: this.appartment,
                    phone: this.phone_number
                };
                axios
                    .post(
                        "https://queen-store.net/azshop/public_html/board/public_html/api/addresses",
                        dd,
                        {
                            headers: {
                                Authorization: "Bearer " + this.user_token
                            }
                        }
                    )
                    .then(res => {
                        this.address_id = res.data.address.id;
                        const checkout = {
                            total_price_before_discount: this.total,
                            discount: this.discount,
                            details: this.order_notes,
                            address_id: this.address_id,
                            phone: this.phone_number
                        };
                        axios
                            .post(
                                "https://queen-store.net/azshop/public_html/board/public_html/api/checkout",
                                checkout,
                                {
                                    headers: {
                                        Authorization:
                                            "Bearer " + this.user_token
                                    }
                                }
                            )
                            .then(() => {
                                axios
                                    .get(
                                        "https://queen-store.net/azshop/public_html/board/public_html/api/carts",
                                        {
                                            headers: {
                                                Authorization:
                                                    `Bearer ` + this.user_token
                                            }
                                        }
                                    )
                                    .then(res => {
                                        this.$parent.cartItemsNo =
                                            res.data.items.length;
                                        this.$parent.cartItems = res.data.items;
                                        this.$parent.cartTotalPrice = 0;
                                        for (
                                            var i = 0;
                                            i <
                                            this.$parent.$parent.cartItems
                                                .length;
                                            i++
                                        ) {
                                            this.$parent.cartTotalPrice =
                                                this.$parent.cartTotalPrice +
                                                this.$parent.cartItems[i]
                                                    .selling_price *
                                                    this.$parent.cartItems[i]
                                                        .quantity;
                                        }
                                    });
                                this.$router.push("/orders");
                            });
                    });
            } else if (this.selectedAddressValue > 0) {
                this.address_id = this.selectedAddressValue;

                const checkout = {
                    total_price_before_discount: this.total,
                    discount: this.discount,
                    details: this.order_notes,
                    address_id: this.address_id,
                    phone: this.phone_number
                };
                axios
                    .post(
                        "https://queen-store.net/azshop/public_html/board/public_html/api/checkout",
                        checkout,
                        {
                            headers: {
                                Authorization: "Bearer " + this.user_token
                            }
                        }
                    )
                    .then(() => {
                        axios
                            .get(
                                "https://queen-store.net/azshop/public_html/board/public_html/api/carts",
                                {
                                    headers: {
                                        Authorization:
                                            `Bearer ` + this.user_token
                                    }
                                }
                            )
                            .then(res => {
                                this.$parent.cartItemsNo =
                                    res.data.items.length;
                                this.$parent.cartItems = res.data.items;
                                this.$parent.cartTotalPrice = 0;
                                for (
                                    var i = 0;
                                    i < this.$parent.$parent.cartItems.length;
                                    i++
                                ) {
                                    this.$parent.cartTotalPrice =
                                        this.$parent.cartTotalPrice +
                                        this.$parent.cartItems[i]
                                            .selling_price *
                                            this.$parent.cartItems[i].quantity;
                                }
                            });
                        this.$router.push("/orders");
                    });
            }
        }
    },
    mounted() {
        window.addEventListener("scroll", this.updateScroll);
        this.user_token = localStorage.getItem("user_token");
        this.user_id = localStorage.getItem("u_id");
        axios
            .get("https://queen-store.net/azshop/public_html/board/public_html/api/carts", {
                headers: { Authorization: "Bearer " + this.user_token }
            })
            .then(res => {
                this.cartItems = res.data.items;
                this.total_before_shipping = res.data.total;
            });

        axios
            .get(
                "https://queen-store.net/azshop/public_html/board/public_html/api/addresses",
                { headers: { Authorization: "Bearer " + this.user_token } }
            )
            .then(res => {
                this.userAddress = res.data.addresses;
            });
            
        axios
            .get(
                "https://localhost/hawa/public/api/districts",
            )
            .then(res => {
                this.districts = res.data.districts;
            });
			
    }
};
</script>
